body {
  background-image: url("/src/assets/background.png");
  background-size: cover;
  font-family: "Poppins", "Courier New", Courier, monospace;
}

nav {
  background-color: transparent;
  color: white;
  position: absolute;
  top: 20;
  z-index: 900;
  overflow: hidden;
  width: 100%;
  padding-top: 10px;
}

nav > h4 {
  cursor: pointer;
  float: left;
  display: block;
  margin-left: 120px;
}

nav > a {
  float: right;
  display: block;
  color: inherit;
  text-decoration: none;
  text-align: center;
  padding-top: 20px;
  margin-right: 40px;
  min-width: 50px;
  font-size: 15px;
}

nav > a.active {
  font-weight: 600;
}

.card {
  text-align: center;
  width: 354px;
  height: 650px;
  color: white;
  background-size: cover;
}

.card-image {
  width: 320px;
  height: 320px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: -80px;
  background-size: cover;
}

.card-content {
  margin: 30px;
}

.token-title {
  text-align: center;
  font-size: large;
  font-weight: 600;
  margin-bottom: 10px;
}

.token-description {
  text-align: justify;
  text-justify: inter-word;
  font-size: small;
  font-weight: 100;
  margin-bottom: 20px;
}

.token-supply {
  font-size: small;
  font-weight: 100;
}

.token-supply > b {
  font-size: medium;
}

.card-action > div {
  cursor: pointer;
  border: 3px solid white;
  border-radius: 45px;
  height: 55px;
  color: white;
  margin: 30px;
}

.card-action > div > p {
  font-family: "Poppins";
  font-weight: 600;
  text-align: center;
  vertical-align: middle;
  line-height: 35px;
  margin: 10px;
}
